import React, {Fragment} from 'react'
import { graphql, useStaticQuery } from "gatsby"

const query = graphql`
  query {
    allContentfulServices {
        nodes {
            simpleText
            header
            image {
                file {
                    url
                }
            }
        }
    }
    allContentfulServiceLines {
        nodes {
            description
        }
    }
  }
`

const Services = () => {
    const data = useStaticQuery(query).allContentfulServices.nodes[0]
    const dataLines = useStaticQuery(query).allContentfulServiceLines.nodes

    return (
        <Fragment>
            {/* Service Left Image Style */}
            <div className="about-area pt-100 pb-100">
                <div className="container-fluid">
                    <div className="row align-items-center">
                        <div className="col-lg-6 col-md-12">
                            <div className="about-img">
                                <img src={data.image.file.url} alt=""/>
                            </div>
                        </div>

                        <div className="col-lg-6 col-md-12">
                            <div className="about-content">
                                <div className="content">
                                    <span className="sub-title">
                                        <h4>{data.header}</h4>
                                    </span>
                                    <ul className="about-list mb-0">
                                        {dataLines.map((service, idx) => (
                                            <li>
                                                <i className="las la-dog" key={idx}></i>
                                                {service.description}
                                            </li>
                                        ))}
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/* End Service Left Image Style */}
        </Fragment>
    )
}

export default Services;