import React from 'react'
import { graphql, useStaticQuery } from "gatsby"
import Loadable from '@loadable/component'
const OwlCarousel = Loadable(() => import('react-owl-carousel3'))

const options = {
    loop: true,
    nav: true,
    dots: false,
    autoplayHoverPause: true,
    autoplay: true,
    margin: 30,
    navText: [
        "<i class='flaticon-left-1'></i>",
        "<i class='flaticon-right-1'></i>"
    ],
    responsive: {
        0: {
            items: 1,
        },
        768: {
            items: 2,
        },
        992: {
            items: 2,
        }
    }
};

const query = graphql`
    query {
        facebook {
            rating_count
            ratings {
                data {
                    created_time(locale: "pt")
                    recommendation_type
                    review_text
                }
            }
        }
    }
`

const Testimonials = () => {
    // Carousel
    const [display, setDisplay] = React.useState(false);
    React.useEffect(() => {
        setDisplay(true);
    }, [])

    const reviews = useStaticQuery(query).facebook

    return (
        <div className="testimonials-area ptb-100 bg-testimonials">
            <div className="container">
                <div className="section-title">
                    <a href="https://www.facebook.com/escolacaes">
                        <span className="sub-title">
                            Siga-Me no Facebook
                        </span>
                    </a>
                    
                    <span><h3>{reviews.rating_count} testemunhos </h3><h2>Testemunhos de Clientes</h2></span>

                </div>

                {display ? <OwlCarousel 
                    className="testimonials-slides owl-carousel owl-theme"
                    {...options}
                > 
                    {reviews.ratings.data.map((list,idx) => (
                        <div className="single-testimonials-item" key={list.idx}>
                            <p>{list.review_text}</p>
                            <span>{list.created_time.substr(0,10)}</span>
                        </div>
                    ))}
                </OwlCarousel> : ''}

            </div>
        </div>
    )
}

export default Testimonials;