import React from 'react'
import ReactWOW from 'react-wow'
import { Link } from 'gatsby'
import { graphql, useStaticQuery } from "gatsby"

import Loadable from '@loadable/component'
const ModalVideo = Loadable(() => import('react-modal-video'))

const query = graphql`
    query {
        allContentfulBanner {
            nodes {
              image {
                file {
                  url
                }
              }
              video
              header
              btnLink
              btnSecondaryLink
              btnSecondaryText
              buttonText
              email1
              email2
              phone1
              phone2
              subheader
              startPhrase
              promotionText
            }
        }
    }
`

const Banner = () => {
    const [isOpen, setIsOpen] = React.useState(true);
    const openModal = () => {
        setIsOpen(!isOpen);
    }
    const data = useStaticQuery(query).allContentfulBanner.nodes[0]
    
    return (
        <React.Fragment>
            <ModalVideo 
                channel='youtube'
                isOpen={!isOpen} 
                videoId={data.video} 
                onClose={() => setIsOpen(!isOpen)} 
            />
            <div className="hero-banner">
                <div className="container">
                    <div className="row align-items-center">
                        <div className="col-lg-6 col-md-12">
                            <div className="hero-banner-content">
                                <ReactWOW delay='.1s' animation='fadeInLeft'>
                                    <h1>{data.header}</h1>
                                </ReactWOW>

                                <ReactWOW delay='.1s' animation='fadeInLeft'>
                                    <h2>{data.subheader}</h2>
                                </ReactWOW>

                                <ReactWOW delay='.1s' animation='fadeInLeft'>
                                    <p>{data.startPhrase}</p>
                                </ReactWOW>

                                <ReactWOW delay='.1s' animation='fadeInRight'>
                                    <div className="row">
                                        <div className="col-md-4">
                                            <div className="btn-box">
                                                <Link to="/contact" className="default-btn">
                                                    Contacte-Me
                                                </Link>
                                                <Link 
                                                        to="#play-video" 
                                                        onClick={e => {e.preventDefault(); openModal()}} 
                                                        className="video-btn popup-youtube"
                                                    >
                                                        <i className="flaticon-google-play"></i>
                                                </Link>
                                            </div>
                                        </div>
                                    </div>
                                </ReactWOW>

                                <ReactWOW delay='.2s' animation='fadeInLeft'>
                                    <br />
                                    <i className="box-icon bx bx-phone" />
                                    <a className="box-icon" href={`tel:${data.phone1}`}> {data.phone1} - </a>
                                    <a className="box-icon" href={`tel:${data.phone2}`}> {data.phone2} </a>    
                                    <br />
                                    <i className="box-icon bx bx-envelope" />
                                    <a className="box-icon" href={`mailto:${data.email1}`}> {data.email1} </a> 
                                    <br />
                                    <i className="box-icon bx bx-envelope" />
                                    <a className="box-icon" href={`mailto:${data.email2}`}> {data.email2} </a>
                                    <br />
                                    <br />
                                </ReactWOW>

                                <ReactWOW delay='.2s' animation='fadeInLeft'>
                                    <div className='additional-phrase'> {data.promotionText} </div>
                                </ReactWOW>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Banner